


































import Vue from "vue";
import GoogleLogin from "vue-google-login";

export default Vue.extend({
  name: "Login",

  components: {
    GoogleLogin,
  },
  methods: {
    onSuccess(data: any) {
      this.$cookies.set("user", JSON.stringify(data));
      this.$router.push("/");
    },
    onFailure(data: any) {
      alert(data);
    },
  },
  data: () => ({
    params: {
      client_id:
        "487249360034-5bq5e0t0a1e2fellutlemiob0f3h7the.apps.googleusercontent.com",
    },
    renderParams: {
      width: 150,
      height: 40,
    },
  }),
});
